<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="16">
        <h1></h1>
        <!-- :model="ruleForm" -->
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="username">
            <el-input type="text" @change="yong" v-model="ruleForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="gender">
            <el-select v-model="ruleForm.gender">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="age">
            <el-input v-model.number="ruleForm.age" ref="search"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="success" @click="submitForm"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="6" style="text-align:right" class="ints">
        <input type="file" @change="gai" ref="bian" id="uploadAvatar" />
        <img v-bind:src="imgUrl" id="avatar" ref="imgs" v-if="imgUrl" />
        <!-- <img  v-bind:src="./avatar.jpg" id="avatar" ref="imgs" v-else> -->
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    var checkAge = (rule, value, callback) => {
      if (value < 18) {
        return callback(new Error("18"));
      } else {
        //
        callback();
      }
    };
    return {
      //idid
      userid: "",
      imgUrl: "/uploads/avatar.jpg",
      ruleForm: {
        canReg: true,
        username: "",
        password: "",
        gender: "",
        age: "",
      },
      rules: {
        age: [
          //
          { required: true, message: "", trigger: "change" },
          { type: "number", message: "", trigger: "change" },
          { validator: checkAge, trigger: "change" },
        ],
        password: [
          {
            min: 6,
            max: 12,
            message: "612",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    //
    async yong() {
      const username = this.ruleForm.username;
      if (username !== "") {
        const result = await fetch(
          `http://47.112.131.23:2007/api/reg/check?username=${username}`,
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        ).then((res) => {
          return res.json();
        });
        console.log("result=", result);
        if (result.code === 0) {
          alert(",");
          this.canReg = false;
        } else if (result.code === 1) {
          this.canReg = true;
        }
      }
    },
    submitForm() {
      this.$refs["ruleForm"].validate(async (valid) => {
        if (valid) {
          //
          console.log(8880);
          const { ruleForm } = this;
          //
          const { data } = await this.$request.put("/insert/", {
            ...ruleForm,
          });
          //
          console.log("", data);
          if (data.code === 1) {
            // this.ruleForm.password= "";
            //  this.ruleForm.gender= "";
            //  this.ruleForm.age= "";  
            // 
          //  this.$router.push({
          //    name:'userlist',
          //  });           
           this.$message({
              type: "success",
              message: "",
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    async gai(e) {
      //
      const { username } = this.ruleForm;
      console.log("", username);
      //   const { id } = this.$route.params;
      //   console.log("id", id);
      const data = new FormData();
      data.set("username", username);
      data.set("avatar", e.target.files[0]);
      const result = await this.$request.post("/insertPic/avatar", data, {
        contentType: false,
        body: JSON.stringify({
          username: username,
        }),
      });
      console.log("",result);
      this.imgUrl = result.data.data.avatarUrl;
    },
  },
  async created() {
    //id
    const { id } = this.$route.params;
    // 
    const { data } = await this.$request.get("/user/" + id);
    // this.userid = id;
    this.imgUrl = data.data.avatarUrl||"/uploads/avatar.jpg"
    console.log("", typeof this.imgUrl);
    // console.log("", id);
    // console.log(typeof data.data.age);
    Object.assign(this.ruleForm, data.data);
  },
};
</script>
<style lang="scss">
.ints img {
  display: block;
  width: 225px;
  height: 200px;
}
</style>