<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="16">
        <h1></h1>
        <!-- :model="ruleForm" -->
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="name">
            <el-input type="text" v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="price1">
            <el-input type="text" v-model.number="ruleForm.price1" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="price2">
            <el-input type="text" v-model.number="ruleForm.price2" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="success" @click="submitForm"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="6" style="text-align:right" class="ints">
        <input type="file" @change="gai" ref="bian" id="uploadAvatar" />
        <img v-bind:src="imgUrl" id="avatar" ref="imgs" v-if="imgUrl" />
        <!-- <img  v-bind:src="./avatar.jpg" id="avatar" ref="imgs" v-else> -->
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    var checkprice = (rule, value, callback) => {
      console.log("value", value);
      if (value < 1) {
        return callback(new Error("0"));
      } else {
        //
        callback();
      }
    };
    return {
      //idid
      userid: "",
      imgUrl: "/uploads/avatar.jpg",
      ruleForm: {
        canReg: true,
        name: "",
        price1: "",
        price2: "",
      },
      rules: {
        price1: [
          //
          { required: true, message: "", trigger: "change" },
          { type: "number", message: "", trigger: "change" },
          { validator: checkprice, trigger: "change" },
        ],
        price2: [
          //
          { required: true, message: "", trigger: "change" },
          { type: "number", message: "", trigger: "change" },
          { validator: checkprice, trigger: "change" },
        ],
        // password: [
        //   {
        //     min: 6,
        //     max: 12,
        //     message: "612",
        //     trigger: "blur",
        //   },
        // ],
      },
    };
  },
  methods: {
    //
    submitForm() {
      this.$refs["ruleForm"].validate(async (valid) => {
        if (valid) {
          //
          const { ruleForm } = this;
          console.log("", this.ruleForm);
          //
          const { data } = await this.$request.put("/insertGoods/", {
            ...ruleForm,
          });
          //
          console.log("", data);
          if (data.code === 1) {
            // this.ruleForm.password= "";
            //  this.ruleForm.gender= "";
            //  this.ruleForm.age= "";
            this.$message({
              type: "success",
              message: "",
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    async gai(e) {
      //
      const { name } = this.ruleForm;
      console.log("", name);
      //   const { id } = this.$route.params;
      //   console.log("id", id);
      const data = new FormData();
      data.set("name", name);
      data.set("avatar", e.target.files[0]);
      const result = await this.$request.post("/insertgoodsPic/avatar", data, {
        contentType: false,
        body: JSON.stringify({
          name: name,
        }),
      });
      console.log("", result);
      this.imgUrl = result.data.data.avatarUrl;
    },
  },
  async created() {
    //id
    const { id } = this.$route.params;
    // 
    const { data } = await this.$request.get("/user/" + id);
    // this.userid = id;
    this.imgUrl = data.data.avatarUrl || "/uploads/avatar.jpg";
    console.log("", typeof this.imgUrl);
    // console.log("", id);
    // console.log(typeof data.data.age);
    Object.assign(this.ruleForm, data.data);
  },
};
</script>
<style lang="scss">
.ints img {
  display: block;
  width: 225px;
  height: 200px;
}
</style>